import React, { useState } from "react";
import styled, { keyframes } from "styled-components";

const Terminal = props => {
  const [isMaximized, setMaximized] = useState(false);

  const TerminalWindow = styled.div`
    width: ${props => (props.maximized ? "99%" : "95%")};
    max-width: ${props => (props.maximized ? "auto" : "800px")};
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
  `;

  const TerminalBg = styled.div`
    background-color: #30353a;
    border-radius: 0 0 0.3em 0.3em;
    height: 100%;
    width: 100%;
    color: white;
  `;

  const TerminalHeader = styled.header`
    background-color: #e0e8f0;
    height: 2em;
    border-radius: 0.3em 0.3em 0 0;
    font-family: monospace;
    text-align: center;
    display: flex;
    flex-grow: 1;
    align-items: center;
    justify-content: center;
    font-weight: 600;
  `;

  const HeaderButton = styled.button`
    background-color: transparent;
    border: none;
    font-size: 1em;
  `;

  const TerminalPrefix = styled.span`
    color: green;
    text-shadow: none;
    font-weight: 600;
  `;

  const TerminalContent = styled.div`
    font-size: 1.3em;
    font-family: monospace;
    padding: 0.5em 0 0 0.5em;
  `;

  const AquaText = styled.span`
    color: #9cdcd4;
  `;

  const blinkKeyframe = keyframes`
    0% {
    opacity: 0;
  }
  49% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
`;

  const Cursor = styled.span`
    color: #fbfbfb;
    animation: ${blinkKeyframe} 1s infinite;
  `;

  return (
    <TerminalWindow maximized={isMaximized} tabIndex="1">
      <TerminalHeader>
        <span style={{ flexGrow: 1 }}>
          #dreamteam@the-internet: ~/dev/bachelor-portfolio
        </span>

        <HeaderButton
          onClick={() => {
            setMaximized(false);
          }}
        >
          &minus;
        </HeaderButton>
        <HeaderButton
          onClick={() => {
            setMaximized(true);
          }}
        >
          +
        </HeaderButton>
        <HeaderButton>&times;</HeaderButton>
      </TerminalHeader>
      <TerminalBg>
        <TerminalContent>
          <TerminalPrefix>➜ dreamteam:~$ </TerminalPrefix>I front-end snakker
          vi:
          <p>
            <AquaText>
              ['Semantisk HTML', 'JavaScript', 'CSS', 'React.js']
            </AquaText>
          </p>
          I back-end har vi erfaring med:
          <p>
            <AquaText> ['Node.js', 'Python', 'Java', 'C#', 'PHP']</AquaText>
          </p>
          Vi har erfaring med skytjenester:
          <p>
            <AquaText> ['Azure', 'Firebase', 'AWS']</AquaText>
          </p>
          Våre foretrukne arbeidsverktøy:
          <p>
            <AquaText>['VsCode', 'IntelliJ', 'Git', 'Trello']</AquaText>
          </p>
          <div style={{paddingBottom: '1em'}}>
            <TerminalPrefix>
              ➜ dreamteam:~$ <Cursor>█</Cursor>
            </TerminalPrefix>
          </div>
        </TerminalContent>
      </TerminalBg>
    </TerminalWindow>
  );
};

export default Terminal;
